.header {
  display: flex;
  justify-content: space-between;
  margin: 1rem 4rem;
}

.header ul li {
  list-style: none;
  margin: 0;
  padding: .5rem 1rem;
  border-radius: 3px;
}

.header ul li:hover {
  background-color: #444;
}

.header a:hover {
  text-decoration: none;
}

.header h3 a {
  color: white;
}

.header h3 a:hover {
  color: #00bc8c;
}

.header button {
  height: 2.2rem;
  margin-top: 3px;
}

.menu-list {
  display: flex;
  margin-top: 2rem;
}